import React from 'react';
import '../assets/css/Reg.css';
import axios from 'axios';
class Reg extends React.Component{
    state={
        username:'',
        password:'',
        url:''

    }
    changeIpt = (ev) => {
        // console.log(ev);
        this.setState({
            [ev.target.name]:ev.target.value
        })
    }
    submit = () => {
        let formData = new FormData();
        formData.append("username", this.state.username);
        formData.append("password", this.state.password);
        formData.append("files",'/img/'+this.refs.d1.files[0].name);
        let request = new XMLHttpRequest();
        request.open("POST", "http://localhost:3000/api/reg",false);
        request.send(formData);
        if(JSON.parse(request.responseText).error==0){
            this.props.history.push('/login')
        }else{
            console.log('注册失败！！！！！')
        }
        // axios({url:'http://localhost:3000/api/reg',method:'post',data:formData}).then(res=>console.log(res))
    }

    render(){
        // console.log('/img/'+this.refs.d1.files[0].name)
        return (
            <div className="reg">
                <div className="uploadDiv">
                        <img  src="../img/touxiang1.jpg"/>
                        <input type="file" ref="d1" className="uploading"/>
                </div>
                <input type="text" name="username" placeholder="请输入名字" className="user" onChange={this.changeIpt}/>
                <input type="password" name="password" placeholder="请输入密码" className="pass" onChange={this.changeIpt} />
                <input type="button" value="注册" className="regBtn" onClick={this.submit}/>
            </div>
        );
    }
}

export default Reg;