import React,{Component} from "react";
import style from "../assets/css/User.module.css";
import {observer, inject} from 'mobx-react'

@inject('store')
@observer
class User extends Component {
    render() {
        let {fensi,guanzhu,url,nikename,lvji}=this.props.store.user.data;
        return (
            <div className={style.user}>
                <div className={style.usermsg}>
                 <img src={url} />
                 <h3>{nikename}</h3>
                 <p>
                     <label>关注:</label><span>{guanzhu}</span>
                     <span className={style.fenge}>|</span>
                     <label>粉丝:</label><span>{fensi}</span>
                 </p>
               </div>
                <ul>
                    <li>
                        <h4>{lvji.title}</h4>
                        <p>{lvji.quwen}</p>
                    </li>
                    <li>
                        <h4>{lvji.title}</h4>
                        <p>{lvji.quwen}</p>
                    </li>
                    <li>
                        <h4>{lvji.title}</h4>
                        <p>{lvji.quwen}</p>
                    </li>
                    <li>
                        <h4>{lvji.title}</h4>
                        <p>{lvji.quwen}</p>
                    </li>
                </ul>
            </div>
        );
    }

}

export default User;