import React,{Component} from "react";
import style from '../assets/css/Cell.module.css'
import {Link} from 'react-router-dom'

class Cell extends Component {
    render() {
        let {cells, dataName} = this.props;
        return (
                    cells.map(item=>(
                        <div className={style.cell}  key={item._id}>
                        <Link
                            to={{pathname:'/detail/'+item._id,search:"?dataName="+dataName+"&"}}
                        >
                            <img src={item.url} />
                            <p>{item.des}</p>
                            <span>{item.title}</span>
                        </Link>
                        </div>

                        )
                    )

        );
    }
}

export default Cell;