import React from 'react';
import style from '../assets/css/Login.module.css';
import { Link } from 'react-router-dom';

import {observer, inject} from 'mobx-react'

@inject('store')
@observer
class Login extends React.Component{
    state={
        username:'',
        password:'',

    }
    changeIpt = (ev) => {
        this.setState({
            [ev.target.name]:ev.target.value
        })
    }
    submit = async () => {
        this.props.store.user.get({
            url:'http://localhost:3000/api/login',
            username:this.state.username,
            password:this.state.password
        }).then(
            (res)=> {
                if (res.error === 0) {
                    //写入local && 跳转user
                    localStorage.setItem('rc_user', JSON.stringify(res));
                    this.props.history.push('/user');
                } else {
                    alert('失败')
                }
            }
        )
    }
    render(){
        return (
            <div className={style.login}>
                <img  src="../img/touxiang1.jpg"/>
                <input type="text" placeholder="请输入名字" value={this.state.username}
                       name="username" className={style.user} onChange={this.changeIpt}/>
                <input type="password" placeholder="请输入密码" value={this.state.password}
                       name="password" className={style.pass} onChange={this.changeIpt}/>
                <input type="button" value="登录" className={style.loginBtn} onClick={this.submit}/>
                <Link to="reg" className={style.goreg}>没有账号？去注册</Link>
            </div>
        );
    }
}

export default Login;