import React,{Component} from "react";
import style from '../assets/css/List.module.css';
import Cell from './Cell';

import {observer, inject} from 'mobx-react';

@inject('store')
@observer
class List extends Component {
    constructor(props){
        super();
        props.store.list.get({url:'http://localhost:3000/api/banner',params:{_limit:4},propsName: 'list'});
    }
    render() {
        let {list} = this.props.store.list;
        return (
            <div className={style.list}>
                <Cell className={style.cell} {...this.props} cells={list} dataName="banner"/>
            </div>
        );
    }
}

export default List;