import React from 'react';
import style from '../assets/css/Detail.module.css';
import querystring from 'query-string'
import {observer, inject} from 'mobx-react'

@inject('store')
@observer
class Detail extends React.Component{
    render(){
        let data = this.props.store.detail.data;
        console.log(data)
        return  (
                   <div className={style.detail}>
                      <img src={data.url} />
                        <div className={style.demsg}>
                            <h3>{data.title}</h3>
                            <p>{data.title1}</p>
                            <div  className={style.gonglue}>
                                <h4>游玩攻略</h4>
                                <p>{data.content}</p>
                        </div>
                        </div>
                    </div>
        );
    }
    async componentDidMount(){
        let id = this.props.match.params.id;
        let dataName = querystring.parse(this.props.location.search).dataName;
        this.props.store.detail.get({url:`http://localhost:3000/api/${dataName}/${id}`});
    }
}

export default Detail;