import React,{Component} from "react";
import '../assets/css/Swiper.css';
import $ from 'jquery';
import Swipe from '../libs/swipe';

class Swiper extends Component {
    render() {
        let {banners} = this.props;
        return (

            <div>
                <div className="banner">
                    <ul className="clear">
                        {
                            banners.map(item => (
                                <li className="bannerli"
                                    key={item._id}
                                >
                                    <img src={item.url} className="bannerimg"/>
                                </li>
                            ))
                        }
                    </ul>

                </div>
            </div>

        );
    }
    componentDidUpdate(){
        new Swipe($('.banner')[0],{
            auto:2000,
            continuous:true,
            stopPropation:true
        })
    }
}
export default Swiper;